.item__pic i { position: absolute; right: 10px; top: 10px; }

.item__desc { color: #000; font-family: Arial; font-size: 12px; font-weight: 400; white-space: nowrap; margin-bottom: 11px; *zoom: 1; }
.item__desc:after { content: " "; display: table; clear: both; }

.item__name { float: left; }

.item__year { float: right; }

.item__title { margin-bottom: 14px; text-transform: uppercase; }

.item__location { color: #000; font-family: Arial; font-size: 11px; font-weight: 400; padding-right: 26px; background: url(../img/location.png) no-repeat top right; display: inline-block; height: 20px; line-height: 20px; float: right; }

.main__bottom { position: relative; text-align: center; }
.main__bottom .btn { margin-bottom: 12px; }

.main__in { margin: 0 -10px; font-size: 0; }

.main__nav { position: relative; float: right; font-size: 0; padding: 15px 9px 0; }
.main__nav a { display: inline-block; color: #000; font-family: Arial; font-size: 16px; padding: 0 12px; text-decoration: none; }
.main__nav a.is-active { font-weight: 700; }

.main__awards { padding-bottom: 62px; }
.main__awards > h3 { padding: 27px 10px 20px; margin-bottom: 0; }

.award { display: inline-block; vertical-align: top; width: 240px; margin: 0 20px 28px; background-color: white; }

.award__pic { margin-bottom: 19px; }

.award__name { margin-bottom: 11px; }

.award__desc { margin-bottom: 0; }

.layout { position: relative; }
.layout .container { padding: 19px 0 0; margin: 0 -5px; }
.layout .col { display: inline-block; vertical-align: top; padding: 0 20px; }
.layout .col1 { overflow: hidden; }
.layout .col2 { width: 280px; }
.layout .col2 p { word-spacing: -0.04em; letter-spacing: -0.01em; }
.layout .col3 { width: 280px; }
.layout .row { padding-bottom: 20px; }
.layout .row h3 { display: inline-block; vertical-align: middle; margin-bottom: 0; }
.layout p { text-align: justify; color: #000; font-family: Arial; font-size: 15px; font-weight: 400; }

.simple-block { position: relative; }
.simple-block p { text-align: left; letter-spacing: -0.01em; }

.gallery { display: inline-block; position: relative; font-size: 0; padding-top: 50px; }

.gallery__thumb { display: inline-block; vertical-align: top; margin: 20px 20px 0 0; }
.gallery__thumb:last-child { margin-right: 0; }

.square { width: 30px; height: 30px; background-color: #fe360a; display: inline-block; vertical-align: middle; margin-right: 21px; }

.brands { position: relative; margin: 0 -15px; padding: 27px 0 0; }

.brand { display: inline-block; vertical-align: top; width: 280px; padding: 0 30px; margin-bottom: 30px; }

.brands__title { padding: 0 30px; }

.brand__pic { margin-bottom: 19px; }

.brand__name { margin-bottom: 11px; }

.brand__desc { margin-bottom: 0; }

.partners { width: 900px; margin: auto; text-align: justify; padding: 42px 0 52px; }

.partners__text { margin-bottom: 0; padding: 0 8px 29px 0; }

.pform { position: relative; width: 720px; }
.pform .btn { width: 140px; height: 40px; background-color: #5cb85c; border: 1px solid #4cae4c; border-radius: 6px; color: white; font-family: Arial; font-size: 16px; font-weight: 700; position: relative; }
.pform .btn:active { top: 1px; }
.pform select.input { -moz-appearance: none; -ms-appearance: none; appearance: none !important; text-indent: 0.01px; position: relative; }
.pform select::-ms-expand { display: none; }
.pform .arrow-bottom { position: relative; display: block; position: absolute; right: 19px; top: 15px; width: 0px; height: 0px; border-style: solid; border-width: 5px 4.5px 0 4.5px; border-color: #000 transparent transparent transparent; pointer-events: none; }

.pform__row { font-size: 0; }
.pform__row:not(:last-child) { margin-bottom: 8px; }
.pform__row [class^="col"] { display: inline-block; vertical-align: middle; position: relative; }
.pform__row .col1 { width: 25%; padding-right: 15px; }
.pform__row .col2 { width: 75%; }

.pform__text { line-height: 20px; margin-bottom: 0; }

.footer { position: relative; font-family: Arial; font-size: 13px; font-weight: 400; }
.footer hr { margin: 37px 0 29px; }
.footer a { font-size: 13px; }

.footer__list { padding-bottom: 61px; *zoom: 1; }
.footer__list:after { content: " "; display: table; clear: both; }
.footer__list li { width: 25%; float: left; }
.footer__list li:nth-last-child(1) { width: 23%; }
.footer__list li:nth-last-child(2) { width: 27%; }

.footer__address { padding-left: 16px; }

.social { text-align: center; font-size: 0; }
.social a { display: inline-block; cursor: pointer; text-decoration: none; }
.social a:not(:last-child) { margin-right: 20px; }

.footer__logo { background-image: url("../img/icons.png"); width: 193px; height: 36px; background-position: 0px -113px; display: block; margin: auto; }

.social__vk { background-image: url("../img/icons.png"); width: 30px; height: 30px; background-position: -346px -172px; }

.social__tw { background-image: url("../img/icons.png"); width: 30px; height: 30px; background-position: -346px -138px; }

.social__fb { background-image: url("../img/icons.png"); width: 30px; height: 30px; background-position: -346px -70px; }

.social__you { background-image: url("../img/icons.png"); width: 30px; height: 30px; background-position: -346px -104px; }

.social__ins { background-image: url("../img/icons.png"); width: 30px; height: 30px; background-position: -346px -36px; }


